.thoughts {
  @apply hidden sm:block relative mt-[45px] mx-[22px] mb-0 pb-[30px];
}

.lamp {
  @apply mx-auto flex justify-center items-center bg-grey-light-2 rounded-full w-[66px] h-[66px];
}

.innerEllipse {
  @apply absolute rounded-full w-[33px] h-[33px] bg-lamp-yellow bg-opacity-70 blur-[17px];
}

.thoughtsText {
  @apply -mt-[33px] pt-[37px] bg-grey-light-2 flex flex-col items-center gap-3 rounded-2xl;
}

.title {
  @apply text-sm font-medium text-pure-black;
}

.description {
  @apply text-xs font-normal text-grey mx-5 text-center;
}

.textButton {
  @apply py-3 px-7 bg-white text-sm font-medium mb-5 rounded;
}
